$fonte_principal : 'Megrim',
'cursive';
$fonte_subtitulo : 'Poppins',
sans-serif;
$font_stroke: 'popins', sans-serif;
$cor-primaria: #DFB162;
$Cor-de-fundo2: #9CBCCC;
$Cor-de-fundo3: #D4D2E3;
$cor-texto-card: #5397C5;
$cor-titulo-card: #053944;

.formStyle {
  width: 300px;
}

@media (min-width: 576px) {
  .formStyle {
    width: 400px;
  }
}

@media (min-width: 768px) {
  .formStyle {
    width: 400px;
  }
}

.input {
  background-color: #EEEEEE;
}

//Cabecalho-----------------------------------------------------------------

.Dropdow {
  background-color: $cor-primaria;
  border: none;

  &:active,
  &:focus {
    background-color: $cor-primaria; // Replace with the color you want
  }

  &:hover {
    background-color: $cor-titulo-card; // Replace with the color you want
  }

  @media (min-width: 800px) {
    display: none;
  }
}

.my-dropdown {
  @media (min-width: 300px) and (max-width: 800px) {
    height: 100%;
    color: #000000;
  }
}

.my-dropdown-toggle {
  @media (min-width: 300px) and (max-width: 800px) {
    height: 100%;
    color: #000000;
  }
}

.my-dropdown-menu {
  @media (min-width: 300px) and (max-width: 800px) {
    height: 600%;
    color: #000000;
  }
}

.my-dropdown-item {
  @media (min-width: 300px) and (max-width: 800px) {
    height: 100%;
    color: #000000;
  }
}

.Cabecalho {
  display: flex;
  padding: 1%;
  height: 100px;
  width: 100%;
  background-color: $cor1_header_footer;
  color: $cor2_header_footer;
  justify-content: space-around;

  @media (min-width: 290px) and (max-width: 340px) {
    width: 100vw;
    height: 70px;
    display: flex;
  }

  @media (min-width: 300px) and (max-width: 800px) {
    width: 100vw;
    height: 70px;
    display: space-around;
  }

  @media (min-width: 800px) and (max-width: 1200px) {
    gap: 0%;
    height: 100px;
    width: 100%;
  }
}

button {
  @media (min-width: 300px) and (max-width: 800px) {
    display: none;
  }
}

.Links {
  display: flex;
  gap: 5%;
  text-decoration: none;
  color: $cor2_header_footer;
  font-size: 20px;
  font-family: $fonte_subtitulo;
  justify-content: center;
  align-items: center;

  @media (min-width: 290px) and (max-width: 340px) {
    flex-direction: column;
  }

  @media (min-width: 300px) and (max-width: 800px) {
    display: none;
    font-size: 10px;
    border-radius: 10px;
  }

  @media (min-width: 800px) and (max-width: 1200px) {
    display: flex;
    font-size: 15px;
    gap: 5%;
  }
}

.link_header:hover {
  color: $cor-primaria;
  font-size: 20px;
  font-family: $fonte_subtitulo;

  @media (min-width: 300px) and (max-width: 800px) {
